/*PROJECTS*/
#projects {
    background: url('../../images/projects.jpg');
/*    opacity: .5;
*/    padding: 3em;
}

.projects {
/*  width: 25em;
*/  height: 20em;
  border-radius: 25px;
  padding: 1em;
}

.proj {
  display: -ms-flexbox;
  display: flex;
  height: 25em;
  padding: 2em;
}


.projDesc {
  width: 25em;
    font-size: .8em;

}

.projButtons {
	-ms-flex-align: center;
	    align-items: center;
	background: white;
}

.btn {
  border-radius: 5px;
  padding: 15px 25px;
  font-size: 1.3em;
  text-decoration: none;
  margin: 20px;
/*  color: #fff;
*/  position: relative;
  display: inline-block;
  cursor: pointer;
}

.btn:active {
  -ms-transform: translate(0px, 5px);
      transform: translate(0px, 5px);
  -webkit-transform: translate(0px, 5px);
  -webkit-box-shadow: 0px 1px 0px 0px;
          box-shadow: 0px 1px 0px 0px;
}

.blue {
    background: #F5F7FA;
    -webkit-box-shadow: 0 .4vw 0 hsl(214, 16%, 86%);
            box-shadow: 0 .4vw 0 hsl(214, 16%, 86%);
    color: black;
}

.blue:hover {
    background: #E6E9ED;
}

.mobile {
  height: 30em;
  width: 15em;
  padding: 1em;
}



